/* Customize the label (the container) */
.imgcheckbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .imgcheckbox .imgcheckbox__label {
    padding: 0;
    margin: 0;
    list-style: none;
    margin-right: 5px;
    cursor: pointer; }
    .imgcheckbox .imgcheckbox__label:last-child {
      margin-right: 0; }

/* Hide the browser's default checkbox */
.imgcheckbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.imgcheckbox__content {
  background: #F3F5F8;
  border-radius: 8px;
  height: 43px;
  display: flex;
  align-items: center;
  padding: 0 18px 0 11px;
  transition: all 0.3s ease; }

/* On mouse-over, add a grey background color */
.imgcheckbox:hover input ~ .imgcheckbox__content {
  background-color: #D9F1FC; }

/* When the checkbox is checked, add a blue background */
.imgcheckbox input:checked ~ .imgcheckbox__content {
  background-color: #D9F1FC; }

/* Create the checkmark/indicator (hidden when not checked) */
.imgcheckbox__content:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.containimgcheckboxer input:checked ~ .imgcheckbox__content:after {
  display: block; }

